<template>
  <div class="itemBox">
    <p class="title">拼团</p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="拼团专区名称">
        <el-input v-model="itemData.title" maxlength="5" placeholder="最多5个字"></el-input>
      </el-form-item>
      <el-form-item label="添加活动商品" class="navigation">
        <div class="goodsUpload">+</div>
        <p class="prompt">按您在此页添加活动商品的顺序,在首页及【查看更多】界面从上到下展示：最多添加20个活动商品</p>
      </el-form-item>
      <el-form-item label="首页展示列表数目" class="navigation">
        <el-radio-group v-model="itemData.listNumber">
          <el-radio :label="0">一个</el-radio>
          <el-radio :label="1">两个</el-radio>
        </el-radio-group>
        <p class="prompt">其他添加的商品会在【查看】更多所代表的二级界面展示出来,您可在设置完活动后,点击【预览】查看</p>
      </el-form-item>
      <el-form-item label="列表样式" class="navigation">
        <el-radio-group v-model="itemData.listType" @change="showType">
          <el-radio :label="0">圆角</el-radio>
          <el-radio :label="1">直角</el-radio>
          <el-radio :label="2">极简圆角</el-radio>
          <el-radio :label="3">圆角有投影</el-radio>
          <el-radio :label="4">直角有投影</el-radio>
          <el-radio :label="5">极简直角</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="显示内容" class="checkboxGroup">
        <el-checkbox v-model="checked" disabled>商品名称</el-checkbox>
        <el-checkbox v-model="checked" disabled>底价</el-checkbox>
        <el-checkbox v-model="checked" disabled>去开团按钮</el-checkbox>
        <el-checkbox v-model="itemData.lineation">划线原价</el-checkbox>
      </el-form-item>
    </el-form>

    <!-- {{itemData}} -->
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      checked: true
    };
  },
  methods: {
    //列表样式
    showType() {
      switch (this.itemData.listType) {
        case 0:
          // 圆角
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 1:
          // 直角
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 2:
          // 极简圆角
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 3:
          // 圆角有投影
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 4:
          // 直角有投影
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 5:
          // 极简直角
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
      }
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .checkboxGroup .el-form-item__content {
    margin-left: 10px;
  }
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 56px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
}
</style>
